<style scoped>
header {
  background: #07111b;
}
header,
nav,
main {
  width: 1920px;
}
nav {
  background: url("https://order.imooc.com/static/module/pay/myorder/img/cart-header-bg.jpg")
    0 0;
}
.nav {
  padding: 36px 0;
  width: 1386px;
  margin: 0 auto;
}
h1 {
  font-size: 36px;
  font-weight: 400;
}
.main {
  background: white;
  width: 1386px;
  margin: -30px auto 0;
  border-radius: 16px;
  box-shadow: 0 8px 16px 0 rgba(7, 17, 27, 0.1);
}
h2 {
  padding: 200px 0;
  text-align: center;
}
.main_header,
.main_centent {
  margin: 0 50px;
  padding: 24px 0;
  border-bottom: 1px solid #eee;
}
.main_header > p:nth-child(1) {
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
}
.main_header > p:nth-child(1) > span:nth-child(1) {
  flex: 1.5;
}
.main_header > p:nth-child(1) > span:nth-child(2) {
  flex: 7;
}
.main_header > p:nth-child(1) > span:nth-child(3) {
  flex: 1.5;
}
.main_header > p:nth-child(1) > span:nth-child(4) {
  flex: 3;
}
.main_header > p:nth-child(1) > span:nth-child(1) > label {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main_centent {
  text-align: center;
  display: flex;
  align-items: center;
}
.main_centent > p {
  flex: 1.5;
}
.main_centent > p:nth-child(1) > input {
  width: 15px;
  height: 15px;
}
.main_centent > p:nth-child(2) {
  display: flex;
  /* justify-content: space-around; */
  align-items: center;
  flex: 7;
}
.main_centent > p:nth-child(2) > img {
  width: 100px;
  margin-right: 50px;
}
.main_centent > p:nth-child(3) {
  flex: 1.5;
}
.main_centent > p:nth-child(4) {
  flex: 3;
}
.main_centent > p:nth-child(4) > input {
  text-align: center;
  width: 30px;
}
.main_centent > p:nth-child(4) > button {
  width: 30px;
}
.main_centent > p:nth-child(4) > button:last-child {
  width: auto;
  margin-left: 20px;
}
footer {
  position: fixed;
  bottom: 0;
  padding: 24px 0;
  width: 100%;
  box-shadow: 10px -2px 12px rgba(7, 17, 27, 0.2);
  background-color: white;
}
.footer {
  width: 1386px;
  margin: 0 auto;
  text-align: right;
  font-size: 16px;
  font-weight: 600;
  color: red;
}
.footer button {
  margin-left: 50px;
}
.mubu {
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  color: red;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
<template>
  <div>
    <header>
      <navs />
    </header>
    <nav>
      <div class="nav">
        <h1>我的购物车</h1>
      </div>
    </nav>
    <main>
      <div class="main">
        <h2 v-if="datas == null">
          无任何数据
        </h2>
        <div v-else class="mainall">
          <div class="main_header">
            <p>
              <span
                ><label>
                  <input type="checkbox" :checked="fall" @click="allcheck" />
                  全选
                </label>
              </span>
              <span>课程</span> <span>价格</span>
              <span>操作</span>
            </p>
          </div>
          <div v-for="(item, index) in datas" :key="index" class="main_centent">
            <p>
              <input
                type="checkbox"
                :checked="item.checkeds"
                @click="check(index)"
              />
            </p>
            <p>
              <img :src="item.pic" alt="" />
              <span>{{ item.shop_title }}</span>
            </p>
            <p>{{ item.money }}</p>
            <p>
              <button @click="reduce(item.id, index)">-</button>
              <input type="text" v-model="item.num" />
              <button @click="add(item.id, index)">+</button>
              <Button type="error" @click="todel(index)">删除</Button>
            </p>
          </div>
        </div>
      </div>
    </main>
    <footer>
      <div class="footer">
        <p>
          总金额:{{ money }} 总数:{{ total }}
          <Button :size="'large'" type="error" @click.native="buy"
            >去结算</Button
          >
        </p>
      </div>
    </footer>
    <div v-if="mubu" class="mubu">
      <h1>正在购买请稍后</h1>
    </div>
  </div>
</template>
<script>
import navs from "../components/nav";

export default {
  components: {
    navs
  },

  data() {
    return {
      mubu: false,
      datas: null,
      money: 0,
      total: 0,
      time: new Date().valueOf(),
      uid: JSON.parse(window.sessionStorage.getItem("user")).id,
      fall: false
    };
  },
  methods: {
    // 全选
    allcheck() {
      this.fall = !this.fall;
      if (this.datas != null) {
        this.datas.forEach((v) => {
          v.checkeds = this["fall"];
          if (this.fall == false) {
            this.total = 0;
            this.money = 0;
          } else {
            this.tonum();
          }
        });
      }
    },
    check(index) {
      this.datas[index].checkeds = !this.datas[index].checkeds;
      var sum = 0;
      this.datas.forEach(v => {
        if (v.checkeds == true) {
          sum++;
        }
        if (sum == this.datas.length) {
          this.fall = true;
        } else {
          this.fall = false;
        }
      });
      this.tonum();
    },

    za() {
      var sum = 0;
      this.datas.forEach(v => {
        if (v.checkeds == true) {
          sum++;
        }
        if (sum == this.datas.length) {
          this.fall = true;
        } else {
          this.fall = false;
        }
      });
    },
    shop() {
      var _this = this;
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/toshops",
        params: {
          uid: this.uid
        }
      }).then(res => {
        this.datas = res.data.data;
        // window.console.log(this.datas);
        if (this.datas != null) {
          this.datas.forEach(function(v, k) {
            _this.datas[k].checkeds = true;
            if (_this.datas[k].checkeds == true) {
              _this.total += v.num;
              _this.money += v.num * v.money;
            }
          });
          this.za();
        }
      });
    },
    //增加
    add(id, index) {
      if (this.datas[index].checkeds == true) {
        this.datas[index].num++;
        this.total++;
        this.money += parseInt(this.datas[index].money);
      } else {
        this.datas[index].num++;
      }
    },
    //减少
    reduce(id, index) {
      if (this.datas[index].checkeds == true) {
        if (this.datas[index].num > 1) {
          this.datas[index].num--;
          this.total--;
          this.money -= parseInt(this.datas[index].money);
        }
      } else {
        if (this.datas[index].num > 1) {
          this.datas[index].num--;
        }
      }
    },
    //提交订单>删除购物车>添加到订单详情
    buy() {
      if (this.total > 0) {
        this.$axios({
          method: "POST",
          url: " http://127.0.0.1:3000/api/buy",
          params: {
            money: this.money,
            uid: this.uid,
            time: this.time,
            num: this.total
          }
        }).then(res => {
          this.mubu = true;
          this.datas.forEach(item => {
            if (item.num > 0&&item.checkeds==true) {
              this.$axios({
                method: "POST",
                url: " http://127.0.0.1:3000/api/delshops",
                params: {
                  uid: this.uid,
                  shopid: item.id
                }
              }).then(ress => {
                if (ress.data.data.msg) {
                  this.$axios({
                    method: "POST",
                    url: " http://127.0.0.1:3000/api/orderinfor",
                    params: {
                      uid: this.uid,
                      shopid: item.id,
                      shopnum: item.num,
                      shoptitle: item.shop_title,
                      buyid: res.data.msg.id
                    }
                  }).then(res => {
                    if (res) {
                      setTimeout(() => {
                        this.mubu = false;
                        this.$router.go(0);
                        alert("结算成功");
                      }, 1000);
                    }
                  });
                }
              });
            }
          });
        });
      }
    },
    //删除
    todel(index) {
      this.$axios({
        method: "POST",
        url: " http://127.0.0.1:3000/api/delshops",
        params: {
          uid: this.uid,
          shopid: this.datas[index].id
        }
      }).then(res => {
        if (res.data.data.msg) {
          alert(res.data.data.msg);
          this.datas[index].checkeds = false;
          this.tonum(index);
          this.datas.splice(index, 1);
        } else {
          alert("网络错误");
        }
      });
    },
    //总计
    tonum() {
      this.total = 0;
      this.money = 0;
      this.datas.forEach((v, k) => {
        if (this.datas[k].checkeds == true) {
          this.total += v.num;
          this.money += v.num * v.money;
        } else {
          this.total = 0;
          this.money = 0;
        }
      });

    
      // if (this.datas[index].checkeds == true) {
      //   this.total += this.datas[index].num;
      //   this.money += this.datas[index].num * this.datas[index].money;
      // } else {
      //   this.total = this.total - this.datas[index].num;
      //   this.money =
      //     this.money - this.datas[index].num * this.datas[index].money;
      //   // this.datas[index].num = 0;
      //   window.console.log(this.datas);
      // }
    }
  },
  mounted() {
    this.shop();
  }
};
</script>
